* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: DM Sans, sans-serif;
}

h1 {
  text-align: center;
  margin-top: 1rem;
}

form {
  justify-content: center;
  width: 100%;
  display: flex;
}

input {
  text-overflow: ellipsis;
  white-space: nowrap;
  background-color: #f5f5f5;
  border: .0625rem solid #0000;
  border-radius: 3.125rem;
  outline: none;
  width: 80%;
  margin-top: 1rem;
  margin-left: 2rem;
  padding: .625rem 5rem .625rem 1.25rem;
  font-size: 1rem;
  transition: border-radius .4s;
  overflow: hidden;
  box-shadow: inset 0 -.0625rem #ffffff80;
}

#searchButton {
  color: #7cbd2b;
  cursor: pointer;
  background: none;
  border: 0;
  width: 1.5rem;
  height: 1.5rem;
  position: relative;
  top: 25px;
  right: 50px;
}

#productsContainer {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.productCard {
  flex: 0 auto;
  width: 310px;
  margin-top: 3rem;
  margin-left: 1rem;
  padding: .5rem;
  position: relative;
  box-shadow: 0 7px 29px #64646f33;
}

.headingDiv {
  justify-content: space-between;
  align-items: start;
  margin-top: .5rem;
  display: flex;
}

.headingDiv h2 {
  max-width: 230px;
}

.productCard h3 {
  margin-top: .5rem;
  margin-bottom: 7rem;
  font-size: 1rem;
  font-style: italic;
  font-weight: 200;
}

.headingDiv p {
  min-width: 55px;
}

img {
  width: 100%;
  height: 200px;
}

.categoryEl {
  margin-bottom: 1rem;
}

.stockEl {
  margin-top: 1rem;
}

.stockElChangeColor {
  color: #ec770a;
}

.productInfoDiv {
  position: absolute;
  bottom: 0;
}

.productCard button {
  cursor: pointer;
  text-align: center;
  color: #0f1111;
  background: #fff;
  border: 1px solid #d5d9d9;
  border-radius: 8px;
  outline: 0;
  height: 31px;
  margin-bottom: .5rem;
  padding: 0 11px;
  font-size: 13px;
  font-weight: 400;
  display: inline-block;
  box-shadow: 0 2px 5px #d5d9d980;
}

.productCard button:hover {
  background-color: #f7fafa;
  border-color: #959999;
}

.buyIcon {
  font-size: 1rem;
}

@media screen and (width >= 790px) {
  body {
    width: 1200px;
    margin: auto;
  }

  h1 {
    text-align: center;
    margin-top: 1rem;
  }

  input {
    width: 50%;
    margin-top: 1rem;
  }
}
